@import url(https://fonts.googleapis.com/css2?family=Style+Script&display=swap);
body {
  margin: 0;
  padding: 0;
  display: flex;
  height: 100vh;

  align-items: center;
  justify-content: center;
  text-align: center;
  color: white;
  font-family: 'Style Script';

  background-image: linear-gradient(270deg, rgb(105, 75, 236), rgb(40, 40, 143), rgb(86, 56, 223), rgb(105, 75, 236));
}

table{
  border-collapse: collapse;
  background-image: linear-gradient(45deg, rgb(40, 40, 143), rgb(105, 75, 236));
}

td{
  border: solid 6px black;
}
td:hover{
  background-color: rgba(255,255,255,0.1);
}
#header{
  font-size: 3rem;
}

div{
  margin: 1rem 0px 1rem 0px;
}

#footer{
  font-size: 1.2rem;
}

#result{
  font-family: 'Courier New', Courier, monospace;
  font-weight: bolder;
  /* font-size: 3rem; */
}
